<template>
  <div class="app" @scroll='navToggle'>

    <transition name='fade'>
      <div class="navs" v-if='up'>
        <div>
          <!-- Logo -->
          <div>
            <img :src="logo" alt="">
          </div>

          <!-- 菜单 -->
          <div>
            <!-- router-link是vue-router内置组件 -->
            <!-- 作用：用于改变url -->
            <!-- tag表示把router-link渲染成什么HTML元素，默认a标签 -->
            <!-- to，表示我被点击时跳转到哪里去 -->
            <!-- exact-active-class表示我被匹配成功，用哪些class类名来显示高亮样式 -->
            <router-link tag='span' exact-active-class='on' to="/zh/">关于我们</router-link>
            <router-link tag='span' exact-active-class='on' to="/zh/products">我们的产品</router-link>
            <!-- 当点击“新闻动态”时，跳转到一个名字叫 n 的路由匹配规则 -->
            <router-link
              tag='span'
              exact-active-class='on'
              :to="{name:'n'}">
              新闻动态
            </router-link>
            <!-- 当点击“社会责任”时，跳转到 '/c'  -->
            <router-link
              tag='span'
              exact-active-class='on'
              :to="{path:'/c'}">
              企业社会责任
            </router-link>
          </div>
        </div>
      </div>
    </transition>

    <!-- 主体内容 -->
    <div class="content">
      <!-- 视图容器，它是由 vue-router 这个库提供的内置组件 -->
      <!-- 作用：用于显示当前url所匹配成功的Vue组件 -->
      <!-- 视图命名：给视图取一个名字，默认叫default -->
      <router-view name='default'></router-view>
      <router-view name='abc'></router-view>
      <router-view name='efg'></router-view>
    </div>
  </div>
</template>

<script>

import logo from './utils/logo.js'

export default {
  components: {
    // TodoList
  },
  data: function() {
    return {
      logo,
      up: true,
      top: 0
    }
  },
  // 监听路由的跳转
  watch: {
    $route: function() {
      // console.log('路由跳转', to, from)
    }
  },
  mounted() {
    console.log('$route', this.$route)
    console.log('$router', this.$router)
  },
  methods: {

    navToggle(e) {
      // 使用鼠标中轮事件实现
      // this.up = e.wheelDelta > 0
      // 使用scroll事件实现
      this.up = (this.top > e.target.scrollTop)
      this.top = e.target.scrollTop
    }
  }
}
</script>

