// cnpm install vue-router -S

import Vue from 'vue'
import VueRouter from 'vue-router'
// 对路由进行注册
Vue.use(VueRouter)

// 路由懒加载，是一种应用程序的性能优化方案
// 只有当url被访问，它所对应的系列组件才会import加载、编译、初始化
const AboutMe = ()=>import('@/pages/AboutMe.vue')
const Product = ()=>import('@/pages/Product.vue')
const News = ()=>import('@/pages/news/News.vue')
const Corporate = ()=> import('@/pages/corp/Corporate.vue')
const CorporateDetail = ()=>import('@/pages/corp/CorporateDetail.vue')

// 两种路由模式：
// Hash路由：url中有一个#，
// History路由：url中没有#

const router = new VueRouter({
  // mode: 'hash',
  mode: 'history',
  // 路由匹配规则
  routes: [
    // 当 url=/zh 时，路由系统要找到一个名字叫default的router-view来承载/显示 AboutMe 这个组件
    // { path: '/zh', component: AboutMe },
    { path: '/zh', components: {default: AboutMe}},
    // 当 url=/zh/products, 路由系统要找到一个名字叫 p 的router-view 来承载/显示 Product 这个组件
    { path: '/zh/products', alias:'/p', components: { abc: Product }},
    // { path: '/xyz', components: {default: AboutMe, abc: Product, efg: News}},

    // alias用于给 path 取一个别名，作用是容易记忆、编写，别名也可以作为url进行访问的
    // name用于给整条路由规则 取一个名字，它代表着一条路由匹配规则
    {
      path: '/zh/news',
      alias:'/n',
      name:'n', component: News,
      // 嵌套路由
      children: [
        {
          path: 'china',
          components: {son: ()=>import('@/pages/news/NewChina.vue')}
        },
        {
          path: 'foreign',
          components: { son: ()=>import('@/pages/news/NewForeign.vue')}
        }
      ],
      redirect: '/zh/news/china'
    },


    { path: '/zh/corporate', alias:'/c', component: Corporate },
    // :id 动态路由，因为 id 不是写死的，它是动态变化的
    { path: '/zh/corporate/detail/:id', name:'cd', alias: '/cd', component: CorporateDetail, props: true },


    // 重定向，当你访问不存在的url时，跳转到指定路由
    // 一般放在路由规则的最后一条
    { path: '/*', redirect: '/zh' }
  ]
})

export default router
